.App {
  text-align: center;
}

.App_logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App_logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App_header {
  background-color: #282c34;
  max-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  color: white;
}

.App_body {
  background-color: #dddede;
  min-height: 85vh;
  display: flex;
  flex-direction: column;
}

.App_h3 {
  font-family: monospace;
  font-size: calc(6px + 4vmin);
}

.App_gallery {
  min-height: 20vh;
  min-width: 20vh;
  width: 80%;
  /* outline: 10px solid #000; */
  margin: 0 auto 3em;

  display: grid;
  grid-template-columns:
    repeat(auto-fit, minmax(min(350px, 100%), 1fr));
  gap: 40px;
}

.App_gallery_img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  outline: 10px solid #000;
  border-radius: calc(4px + 2vmin);
  
}

.App_gallery_img:hover {
  outline: 10px solid #61dafb;
  transition: background-color 0.5s;
  transform: scale(1.12);

}

.App_gallery_img:focus {
  outline-color: #61dafb;
}

.App_card {
  background-color: rgb(28, 35, 35);
  color: white;
  display: inline-block;
  height: auto;
  width: auto;
}

.App_link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
